export class TrafficLight {
    constructor(databaseRecord) {
        Object.defineProperty(this, "state", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: 'happy'
        });
        Object.defineProperty(this, "id", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "uuid", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "name", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        Object.defineProperty(this, "dashboardId", {
            enumerable: true,
            configurable: true,
            writable: true,
            value: void 0
        });
        this.id = databaseRecord.id;
        this.uuid = databaseRecord.uuid;
        this.name = databaseRecord.name;
        this.dashboardId = databaseRecord.dashboardId;
    }
    makeHappy() {
        if (this.state === 'happy') {
            return;
        }
        this.state = 'transitioning';
        setTimeout(() => {
            this.state = 'happy';
        }, 2000);
    }
    makeSad() {
        if (this.state === 'sad') {
            return;
        }
        this.state = 'transitioning';
        setTimeout(() => {
            this.state = 'sad';
        }, 2000);
    }
}
